import { Autocomplete, TextField } from '@mui/material';
import React, { useEffect, useState } from 'react';
import MetaForm from '../../constants/MetaForm';
import { IConfig, IField, IMeta, TParam, TParamType } from '../../constants/model-interfaces';
import FormUtils from '../../utils/FormUtil';
export function MandatoryLabel() {
    return (
        <span>*</span>
    )
}

export function Search(props: {name: string, form: IMeta, variant: string, config: IConfig, label: string,
    handleChange: Function, handleValidation: Function, context: MetaForm, section: string, error:{hasError: boolean, errorMsg: string}}) {
    const [loading, setLoading] = useState(false);
    const [options, setOptions] = useState(props.form.options || []);
    const [value, setValue] = useState({label:'', value: props.form.value});
    const inputEv = props.form.events?.input;
    const wrapperClassName = 'meta-form-control-' + props.name;
    useEffect(() => {
        if (props.form.options && props.form.options.length > 0) {
            const value = FormUtils.getSearchValue(props.form.options, props.form.value);
            setOptions(props.form.options);
            setValue(value);
        }
    },
    [props.form.options]);
    
    return (
        <Autocomplete 
            value={value}
            options={options}
            loading={loading}
            className={wrapperClassName}
            getOptionLabel={(option: any)=>option.label}
            isOptionEqualToValue={(option, value) => value && value.value === option.value}
            onChange={(e, val) => {
                const actualValue = val?.value ? val.value : inputEv?.value && val && val[inputEv.value]
                    ? val[inputEv.value] : '';
                props.handleChange(e, actualValue, val);
                setValue(val);
            }}
            onBlur={()=> props.handleValidation()}
            onInputChange={(e:any, val:string) => {
                let url = inputEv?.url || '';
                if (url) {
                    const params = props.form.events?.input?.params;
                    let query = '';
                    params && params.forEach((param: TParam, index: number) => {
                        const [name, value] = param;
                        if (typeof value === 'undefined') {
                            query+= `&${name}=${val}`;
                        } else if (typeof value === 'string') {
                            query+= `&${name}=${value}`;
                        } else if (typeof value === 'object') {
                            const type = value['type'];
                            switch (type) {
                                case 'fieldValue':
                                    const ref = value.ref;
                                    if (ref) {
                                        const section = value.section ? value.section : props.section;
                                        const field = props.context.getField(section, ref);
                                        const fielfValue = field?.value;
                                        query+= `&${name}=${fielfValue}`;
                                    }
                                    break;
                            }
                        }
                    });
                    url = query ? url + `?${query}` : url;
                }
                setLoading(true);
                props.context.api('get', url)
                    .then(response => {
                        const responseKey = inputEv?.response;
                        const results = responseKey ? response[responseKey] : response;
                        let newResults = [];
                        if (results && inputEv?.label && inputEv?.value) {
                            newResults = results.map((r:any) => {
                                return {label:r[inputEv?.label!],value:r[inputEv?.value!],ref:r};
                            })
                        } else {
                            newResults = results;
                        }
                        setOptions(newResults);
                        setLoading(false);
                    });
            }}
            renderInput={(params:any) => <TextField {...params} 
                    label={props.label}
                    variant={props.variant}
                    error={props.error.hasError}
                    helperText={props.error.errorMsg}
                />}
        />
    )
}