import React, { SyntheticEvent, useState } from 'react';
import { IButtonTypes } from '../constants/common-interface';
import { IField, ISchema, ITheme } from "../constants/model-interfaces";
import FormUtils from "../utils/FormUtil";
import Submit from "./form-controls/Submit";
import FormFieldRenderer from './form-field-renderer';
import SectionLayout from './SectionLayout';

export default function (props: IProps) {
    const hasSection = FormUtils.hasSections(props.schema.fields);
    const sectionLayout = props.theme.sectionLayout;
    const formButtons = props.schema.buttons && props.schema.buttons.length > 0
        ? props.schema.buttons : FormUtils.getFormDefaultButtons();
    const handleSubmit = (e: any, field: IField) => {
        props.validate(e,'submit');
        if (props.onSubmit) {
            const data = props.form;
            props.onSubmit(e, data);
        }
    }
    const handleReset = (e: any, field: IField) => {
        // to do
    }
    const handleNext = (e: SyntheticEvent, section: IField, callback: any) => {
        if (props.validate(e, 'next')) {
            props.emit('switch', {payload:'next',callback});
        } else {
            props.emit('validation_error', {payload:section.name});
        }
    }
    const handlePrevious = () => {
        props.emit('switch', {payload:'previous'});
    }
    if (hasSection && sectionLayout !== 'default') {
        return (
            <form className={`needs-validation ${props.validated ? 'was-validated' : ''}`} noValidate>
                <SectionLayout fields={props.schema.fields} form={props.form} />
                <Submit theme={props.theme.type}
                    form={props.form}
                    buttons={props.buttons}
                    formButtons={formButtons}
                    onSubmit={handleSubmit}
                    onReset={handleReset}
                    onNext={handleNext}
                    onPrevious={handlePrevious}
                />
            </form>
        )
    } else {
        const [fields, setFields] = useState(props.schema.fields);
        const sync = () => {
            setFields([...fields]);
        }
        return (
            <form className={`needs-validation ${props.validated ? 'was-validated' : ''}`} noValidate>
                <div className='row'>
                {
                    props.schema.fields.map((field: IField) => <FormFieldRenderer {...field} key={field.name} section='default'
                        form={hasSection ? props.form[field.name] : props.form['default'][field.name]} sync={sync} />)
                }
                </div>
                <Submit theme={props.theme.type}
                    form={props.form}
                    buttons={props.buttons}
                    formButtons={formButtons}
                    onSubmit={handleSubmit}
                    onReset={handleReset}
                    onNext={handleNext}
                    onPrevious={handlePrevious}
                />
            </form>
        )
    }
}

interface IProps {
    schema: ISchema;
    validated: boolean;
    form: any;
    theme: ITheme;
    onSubmit: Function;
    validate: (e: SyntheticEvent, type: string) => boolean;
    emit: Function;
    buttons?: IButtonTypes;
}