import EventEmitter from "eventemitter3";
import { type } from "os";
import { MSGS, Page, Rest } from ".";
import FormUtils from "../utils/FormUtil";
import { IMetaForm, IForm, IFormField, IError } from "./common-interface";
import { IDependency, IField, IRest, ISchema, ITheme, TParam } from "./model-interfaces";

export default class MetaForm implements IMetaForm {
    theme: ITheme;
    form: IForm;
    rest: Rest;
    page: Page;
    constructor(private schema: ISchema, private eventEmitter: EventEmitter) {
        this.theme = schema?.theme;
        this.form = {};
        this.rest = new Rest(schema.rest);
        let totalSections = 0;
        if (schema.fields) {
            const hasSections = FormUtils.hasSections(schema.fields);
            if (hasSections) {
                schema.fields.forEach(section => {
                    this.setSection(section.name);
                    section.fields && section.fields.forEach(field => {
                        this.initField(section.name, field);
                        // this.setFieldData(section.name, field);
                    });
                    totalSections++;
                });
                this.initDependencies();
                this.applyDependencies();
            } else {
                const section = 'default';
                this.setSection(section);
                schema.fields && schema.fields.forEach(field => {
                    this.initField(section, field);
                    // this.setFieldData('default', field);
                });
                this.initDependencies();
                this.applyDependencies();
            }
        }
        if (this.theme && 
            this.theme.sectionLayout === 'tabs' 
            || this.theme.sectionLayout === 'stepper' 
            || this.theme.sectionLayout === 'wizard') {
                this.page = new Page(true, totalSections);
            } else {
                this.page = new Page(false, 1);
            }
    }
    /** page functions */
    getPage() {
        return this.page;
    }
    setPage(page: Page) {
        this.page = page;
    }
    updatePage(pageNumber: number) {
        this.page.update(pageNumber);
        this.eventEmitter.emit('page_change');
    }
    /** event emitter functions */
    emit(eventType: string, payload: any) {
        this.eventEmitter.emit(eventType, payload);
    }
    listener(eventType: string, fn: (...args: any[]) => void) {
        this.eventEmitter.addListener(eventType, fn);
    }
    removeListener(event: string, fn?: (...args: any[]) => void) {
        this.eventEmitter.removeListener(event, fn);
    }
    destroy() {
        this.eventEmitter.removeAllListeners();
    }
    /** rest functions */
    getRestConfig() {
        return this.rest?.config;
    }
    api(type: string, url: string, params?: Array<TParam>, payload?: any) {
        let query = '';
        params && params.forEach((param: TParam, index: number) => {
            const [name, value] = param;
            if (['string', 'boolean', 'number'].indexOf(typeof value)>=0) {
                query+= `&${name}=${value}`;
            } else if (typeof value === 'object') {
                const type = value['type'];
                switch (type) {
                    case 'fieldValue':
                        const ref = value.ref;
                        if (ref) {
                            const section = value.section||'';
                            const field = this.getField(section, ref);
                            const fielfValue = field?.value;
                            query+= `&${name}=${fielfValue}`;
                        }
                        break;
                }
            }
        });
        const newUrl = query ? `${url}?${query}` : url;
        switch(type) {
            default: 
                return this.rest.get(newUrl);
        }
    }
    /** Theme functions */
    getThemeProp(themeName: string, prop: string) {
        if (this.theme[themeName]) {
            return this.theme[themeName][prop];
        }
    }

    initDependencies() {
        this.schema.fields.forEach(section => {
            section.fields && section.fields.forEach(field => {
                this.setDependencies(section.name, field.name, field?.meta?.dependencies);
            });
        });
    }

    setFieldData(section: string, field: IField) {
        switch (field.meta.displayType) {
            case 'select':
            case 'checkbox':
            case 'radio':
                this.setFieldOptions(section, field.name, field.meta.options);
                break;
            case 'number':
                // if (field.meta.min !== undefined) {
                //     this.setFieldProp(section, field.name, 'min', field.meta.min);
                // }
                // if (field.meta.max !== undefined) {
                //     this.setFieldProp(section, field.name, 'max', field.meta.max);
                // }
                // if (field.meta.isReadonly) {
                //     this.setFieldProp(section, field.name, 'readOnly', true);
                // }
                break;
            case 'text':
            case 'email':
            case 'text_field':
            case 'date':
                if (field.meta.isReadonly) {
                    this.setFieldProp(section, field.name, 'isReadonly', true);
                }
                break;
        }
    }
    
    setSection(section: string) {
        this.form[section] = {};
    }
    initField(section: string, field: IField) {
        this.form[section][field.name] = {
            prop: field.prop,
            display: true, 
            type: field.meta.type,
            value: field?.meta?.value !== undefined ? field?.meta?.value : '',
            isDisabled: field.meta.isDisabled,
            isReadonly: field.meta.isReadonly,
            displayName: field.meta.displayName,
            displayType: field.meta.displayType,
            options: field.meta.options,
            isArray: field.meta.isArray,
            url: field.meta.url,
            validation: field.meta.validation,
            mui: field.meta.mui,
            bootstrap: field.meta.bootstrap,
            className: field.meta.className,
            events: field.meta.events,
            fieldLayout: field.meta.fieldLayout,
            optionsLayout: field.meta.optionsLayout,
            labelPlacement: field.meta.labelPlacement,
            error: {hasError: false, errorMsg: ''}
        };
    }
    getField(section: string, field: string) {
        return this.form[section][field];
    }
    setField(section: string, field: string,value: any) {
        this.form[section][field].value = value;
    }
    setFieldProp(section: string, field: string,prop: any, propVal: any) {
        this.form[section][field][prop] = propVal;
    }
    getFieldDisplay(section: string, field: string) {
        return this.form[section][field].display;
    }
    setFieldDisplay(section: string, field: string, display: boolean) {
        this.form[section][field].display = display;
    }
    getFieldOptions(section: string, field: string) {
        return this.form[section][field].options || [];
    }
    setFieldOptions(section: string, field: string, options: Array<any> | undefined) {
        this.form[section][field].options = options; 
    }
    setFieldDisabled(section: string, field: string, disabled: boolean) {
        this.form[section][field].isDisabled = disabled ? true : undefined;
    }
    getDependencies(section: string, field: string) {
        return this.form[section][field]['dependencies'];
    }
    getChangeEvents(section: string, field: string) {
        return this.form[section][field]?.events?.change;
    }
    setDependencies(section: string, field: string, dependencies: any) {
        if (!dependencies) return;
        if (dependencies.exists) {
            const ref = dependencies.exists.ref;
            const value = dependencies.exists.value;
            const type = 'exists';
            const formSection = dependencies.exists.section ? dependencies.exists.section : section;
            if (!this.form[formSection][ref]['dependencies']) {
                this.form[formSection][ref]['dependencies'] = [];
            }
            this.form[formSection][ref]['dependencies'].push({section, type, field, value});
        }
        if (dependencies.load) {
            const ref = dependencies.load.ref;
            const url = dependencies.load.url;
            const formSection = dependencies.load.section ? dependencies.load.section : section;
            if (!this.form[formSection][ref]['dependencies']) {
                this.form[formSection][ref]['dependencies'] = [];
            }
            this.form[formSection][ref]['dependencies'].push({section, type:'load', field, url});
        }
        if (dependencies.load_options) {
            const ref = dependencies.load_options.ref;
            const valueMap = dependencies.load_options.valueMap;
            const formSection = dependencies.load_options.section ? dependencies.load_options.section : section;
            if (!this.form[formSection][ref]['dependencies']) {
                this.form[formSection][ref]['dependencies'] = [];
            }
            this.form[formSection][ref]['dependencies'].push({section, type:'load_options', field, valueMap});
        }
        if (dependencies.enabled) {
            const ref = dependencies.enabled.ref;
            const value = dependencies.enabled.value;
            const formSection = dependencies.enabled.section ? dependencies.enabled.section : section;
            if (!this.form[formSection][ref]['dependencies']) {
                this.form[formSection][ref]['dependencies'] = [];
            }
            this.form[formSection][ref]['dependencies'].push({section, type:'enabled', field, value});
        }
        if (dependencies.equals) {
            const ref = dependencies.equals.ref;
            const value = dependencies.equals.value;
            const formSection = dependencies.equals.section ? dependencies.equals.section : section;
            if (!this.form[formSection][ref]['dependencies']) {
                this.form[formSection][ref]['dependencies'] = [];
            }
            this.form[formSection][ref]['dependencies'].push({section, type:'equals', field, value, currentValue: dependencies.equals.currentValue, resetValue: dependencies.equals.resetValue});
        }
        if (dependencies.displayType) {
            const ref = dependencies.displayType.ref;
            const value = dependencies.displayType.value;
            const formSection = dependencies.enabled.section ? dependencies.enabled.section : section;
            if (!this.form[formSection][ref]['dependencies']) {
                this.form[formSection][ref]['dependencies'] = [];
            }
            this.form[formSection][ref]['dependencies'].push({section, type:'displayType', field, value, currentValue: dependencies.equals.currentValue, resetValue: dependencies.equals.resetValue});
        }
        if (dependencies.not_exists) {
            const ref = dependencies.not_exists.ref;
            const formSection = dependencies.not_exists.section ? dependencies.not_exists.section : section;
            if (!this.form[formSection][ref]['dependencies']) {
                this.form[formSection][ref]['dependencies'] = [];
            }
            this.form[formSection][ref]['dependencies'].push({section, type:'not_exists', field, value: dependencies.not_exists.value});
        }
    }
    applyDependencies() {
        this.schema.fields.forEach(section => {
            section.fields && section.fields.forEach(field => {
                
                const deps = this.getDependencies(section.name, field.name);
                const pField = this.getField(section.name, field.name);
                const value = pField.value;
                deps && deps.forEach((dep: {type: string, section: string, field: string, value: any, url: string, currentValue: any, resetValue: string}) => {
                    switch (dep.type) {
                        case 'exists':
                            {
                                const depValue = dep.value;
                                const field = dep.field;
                                if (depValue === undefined) {
                                    // field dependency

                                } else {
                                    // condition dependency
                                    if (value != undefined) {
                                        let conditionMatch = false;
                                        switch (depValue) {
                                            case '$notempty':
                                                conditionMatch = value !== '';
                                            break;
                                            default:
                                                conditionMatch = value === depValue;
                                        }
                                        this.setFieldDisplay(dep.section, field, conditionMatch);
                                    }
                                }
                            }
                            break;
                        case 'enabled': {
                            const field = dep.field;
                            this.setFieldDisabled(dep.section, field, value !== dep.value);
                        }
                        break;
                        case 'equals': {
                            if (value === dep.value) {
                                this.setField(dep.section, dep.field, dep.currentValue);
                            } else if (dep.resetValue !== undefined) {
                                this.setField(dep.section, dep.field, dep.resetValue);
                            }
                        }
                        break;
                        case 'load': {
                            if (value) {
                                const url = dep.url.replace('{0}', value);
                                const field = dep.field;
                                fetch(url).then(res => res.json()).then(result => {
                                    this.setFieldOptions(dep.section, field, result);
                                });
                            }
                        }
                        break;
                        case 'set': {
                            if (value) {
                                // to do
                            }
                        }
                    }
                });
            });
        });
    }

    handleDependencies(section: string, fieldName: string, value: any, fieldDisplayed: boolean) {
        return new Promise((sucessCallback: Function) => {

            const deps = this.getDependencies(section, fieldName);
            deps && deps.forEach((dep: {
                section: string, 
                type: string, 
                value: string, 
                valueMap: Array<any>,
                field: string, 
                url: string, 
                currentValue: any, 
                resetValue: any})=>{
                const type = dep.type;
                switch(type) {
                    // case 'not_exists': {
                    //     const depValue = dep.value;
                    //     const field = dep.field;
                    //     if (value !== undefined) {
                    //         this.setFieldDisplay(dep.section, field, value !== depValue);
                    //         // cascade
                    //         const subfield = this.getField(dep.section, field);
                    //         this.handleDependencies(dep.section, field, subfield.value, value !== depValue);
                    //     }
                    // }
                    // break;
                    case 'exists': {
                        const depValue = dep.value;
                        const field = dep.field;
                        if (fieldDisplayed) {
                            if (value != undefined) {
                                if (depValue === undefined) {
                                    // only field dependency
                                    this.setFieldDisplay(dep.section, field, true);
                                } else {
                                    // condition dependency
                                    let conditionMatch = false;
                                    switch (depValue) {
                                        case '$notempty':
                                            conditionMatch = value !== '';
                                            break;
                                        default:
                                            conditionMatch = value === depValue;
                                    }
                                    this.setFieldDisplay(dep.section, field, conditionMatch);
                                }
                                const subfield = this.getField(dep.section, field);
                                // cascade
                                this.handleDependencies(dep.section, field, subfield.value, value === depValue);
                            }
                        } else {
                            this.setFieldDisplay(dep.section, field, false);
                            const subfield = this.getField(dep.section, field);
                            // cascade
                            this.handleDependencies(dep.section, field, subfield.value, false);
                        }
                    }
                    break;
                    case 'load': {
                        const field = dep.field;
                        const url = dep.url.replace('{0}', value);
                        FormUtils.loader(url).then((res:any) => {
                            this.setFieldOptions(dep.section, field, res);
                            sucessCallback(true);
                        });
                    }
                    break;
                    case 'load_options':{
                        const field = dep.field;
                        const val = dep.value;
                        const valueMap = dep.valueMap;
                        if (valueMap !== undefined) {
                            if (valueMap[value]) {
                                this.setFieldOptions(dep.section, field, valueMap[value]);
                                sucessCallback(true);
                            }
                        } else {
                            if (value) {
                                // to do
                            }
                        }
                    }
                    break;
                    case 'enabled': {
                        const field = dep.field;
                        this.setFieldDisabled(dep.section, field, value !== dep.value);
                    }
                    break;
                    case 'equals':
                        const field = dep.field;
                        if (value === dep.value) {
                            this.setField(dep.section, field, dep.currentValue);
                        } else if (dep.resetValue !== undefined) {
                            this.setField(dep.section, field, dep.resetValue);
                        }
                    break;
                }
            });
            sucessCallback(true);
        })
    }

    /** validation functions */
    setError(section: string, field: string, error: IError) {
        this.setFieldProp(section, field, 'error', error);
    }

    validate() {
        let hasErrors = false;
        if (this.page.isGrouped) {
            // validate page
            const section = this.schema.fields.find((section, index) => index + 1=== this.page.pageNumber);
            if (section) {
                const sectionName = section.name;
                if (this.form && this.form[sectionName]) {
                    Object.keys(this.form[sectionName]).forEach(field => {
                        const formField: IFormField = this.form[sectionName][field];
                        if (formField.display && formField.validation?.required) {
                            // for required field
                            if (formField.value !== false && formField.value !== 0) {
                                if (!formField.value) {
                                    formField.error.hasError = true;
                                    formField.error.errorMsg = formField?.validation?.required_detail?.errorMsg
                                        ? formField.validation.required_detail.errorMsg : MSGS.ERROR_MSG.REQUIRED;
                                    hasErrors = true;
                                }
                            }
                        }
                    });
                }
            }
        } else {

        }
        return !hasErrors;
    }
}