import React, {Fragment, useContext, useEffect, useState} from 'react';
import MuiFormControl from './mui-form-control';
import BSFormControl from './bs-form-control';
import { IRenderField } from '../../constants/common-interface';
import FormContext from '../form-context';
import { IField } from '../../constants/model-interfaces';
import FormUtils from '../../utils/FormUtil';

function FormControlType(props: any) {
    const [f, set] = useState<Array<IField>>([]);
    const type = props.props?.meta?.type ? props.props.meta.type : '';
    const add = (e: any) => {
        const field = {name: '',meta:{}, fields: props.props.fields};
        f.push(field);
        set([...f]);
        e.preventDefault();
    }
    const remove = (e: any, index: number) => {
        e.preventDefault();
        f.splice(index,1);
        set([...f]);
    }
    const vs = FormUtils.getVerticalSpacing(props.theme);
    switch (type) {
        case 'group':
            return (
                <Fragment>
                    {
                        f.map((fi: IField, index: number) => {
                            return (
                                <div className='col-md-12'>
                                    <div className={"row " + vs}>
                                        {
                                            fi.fields && fi.fields.map(field => {
                                                return (
                                                    <div className="col">
                                                         <FormControl {...props}/>
                                                    </div>
                                                )
                                            })
                                        }
                                        <div className="col"></div>
                                        <div className="col-2">
                                            <button onClick={(e)=>remove(e, index)}>Remove</button>
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                    }
                    {/* <button onClick={add}>Add more</button> */}
                </Fragment>
            )
        default:
            return (
                <FormControl {...props}/>
            )
    }
}

export function FormControl({theme, props, cField, sync}:{theme: string, props: IRenderField, cField: any, sync: Function}) {
    switch (theme) {
        case 'mui':
            return (
                <MuiFormControl {...props} sync={sync}/>
            )
        case 'tamagui':
            return (
                // <TamaGuiFormControl {...props} />
                <Fragment></Fragment>
            )
        case 'bootstrap':
        default:
            return (
                <BSFormControl {...props} sync={sync}/>
            )
    }
}

export default FormControlType;